import React from 'react';
import {imagenesPortada} from '../assets/imagenes.js'
import {imagenesPizza, imagenesHamburguesas,imagenesEnsaladas} from '../assets/imagenes.js'
import {

    Link
  } from "react-router-dom";
export function Home (){
return(
    <>
    <div>
    <h2 className="homeTitulo">{imagenesPortada[0].titulo}</h2>
    <hr/>
    </div>
    
    <div className="Portadacontain">
               
        <Link to="/Comidas" style={{ textDecoration: 'none'  }} className="ofertaalert" >ORDENÁ AHORA!</Link>
            
                

        
        <img className="homeimagenPortada"src={imagenesPortada[0].img} alt="pizza con huevo"></img> 
    </div>
    <hr className="hrvariant"/>
    
    <fieldset>

       <legend>MENÚ</legend>
          <section className="contenedorlinks">

          <figure className="figurecontainer">
           <p className="descripcionlink">Pizzas</p>    
          <img className="linksacomidas" alt="links a comidas" src={imagenesPizza[1].img} ></img>
          
          </figure>

          <figure className="figurecontainer">
           <p className="descripcionlink">Hamburguesas</p>     
          <img className="linksacomidas" alt="links a comidas" src={imagenesHamburguesas[0].img} ></img>
          </figure>

          <figure className="figurecontainer" >
           <p className="descripcionlink">Ensaladas</p>      
          <img className="linksacomidas" alt="links a comidas" src={imagenesEnsaladas[0].img} ></img>
          </figure>

          </section>



   </fieldset>

        
     
    
    
     </> )


}