import React, { useState } from 'react';
import {imagenesPizza, imagenesHamburguesas,imagenesEnsaladas} from '../assets/imagenes.js'
import Comidas from '../components/Comidas.js'



export default function SetComidas(){
 
 



const [tipodeMenu, settipodeMenu] = useState("");



    return(
<>
        
        <h1 className="menutitulo">{tipodeMenu.toUpperCase()||"Pizzas" }</h1>

        <hr></hr>

        <nav className="selector-comidas">
              <li onClick={()=> settipodeMenu("Pizzas")}className="opcionesdecomidas" >
              Pizzas
              </li>
              <li onClick={()=> settipodeMenu("Hamburguesas")}className="opcionesdecomidas" >
              Hamburguesas
              </li>
              <li onClick={()=> settipodeMenu("Ensaladas")}className="opcionesdecomidas" >
              Ensaladas
              </li>


         
         
            
        </nav>
        <hr></hr>

         <div className="ventana">
            
         {tipodeMenu === ""&& imagenesPizza.map((el)=>
                    (
  
                       <Comidas key={el.title} title={el.title} img={el.img} precio={el.precio}calorias={el.calorias} />
   
                    )
               )}
         {tipodeMenu === "Pizzas"&& imagenesPizza.map((el)=>
                    (
  
                       <Comidas key={el.title} title={el.title} img={el.img} precio={el.precio}calorias={el.calorias} />
   
                    )
               )}

        {tipodeMenu === "Hamburguesas"&& imagenesHamburguesas.map((el)=>
                    (
  
                       <Comidas key={el.title} title={el.title} img={el.img} precio={el.precio} calorias={el.calorias} />
   
                    )
               )}    


        {tipodeMenu === "Ensaladas"&& imagenesEnsaladas.map((el)=>
                    (
  
                       <Comidas key={el.title} title={el.title} img={el.img} precio={el.precio}calorias={el.calorias} />
   
                    )
               )} 




 

         </div>


      
      </>
    )
}