import React from 'react';

import BotonCompra from '../components/BotonCompra.js'

export default function Comidas(props){
   
    return(
    <> 

    
     <div className="miniventana">

      
      <div className="caracteristicasminiventana" >
          
          <p className="tituloscomidas">{props.title}</p>
          <p className="precioscomidas">{props.precio}</p>
          <BotonCompra/>
          <p className="caloriascomidas">{props.calorias}</p>
      </div>  
      <section className="containerimage">
          <figure>
          <img className="imagenesdelascomidas"src={props.img} alt="pizza con huevo"></img>
          </figure>
      </section>
    
    
    </div>
    </>
    
    )
    
    
    }

    