import React from 'react';

import {imagengoogleappleStore } from '../assets/imagenes'
export function AppDownload(){

    return(
        <>
                <div className="formularioapp">
                  
                    <div className="estiloformularioapp">
                       <h1 className="sumate">DESCARGÁ NUESTRA APP AQUÍ!</h1>
                       <p> Descargala para disfrutar de los mejores beneficios, descuentos y novedades!    
                       </p>
                            
                            <div className="botoncontainerapp">
                                  
                                    <img src={imagengoogleappleStore[0].img} alt={imagengoogleappleStore[0].title}></img>
                                    <img src={imagengoogleappleStore[1].img} alt={imagengoogleappleStore[1].title}></img>
                            </div>
                            
                    </div>
                
                </div>
                


        </>
    )
}