import React from 'react';
import {

  Link
} from "react-router-dom";



export default function MenumiApp(){

 



    return(
       <> 
       
        <nav  className="menu-HamburgerHouse">
        
        <details open >

           <summary><span>MENÚ</span></summary> 
           
           < ul> 
              <span className="spandelmenu">
              <li  >
              <Link to="/Home" style={{ textDecoration: 'none' }} className="opcionesdelmenu" >INICIO</Link>
              </li>
              </span>

              <span className="spandelmenu">

              <li  >
              <Link to="/Comidas" style={{ textDecoration: 'none'  }}  >MENU</Link>
              </li>
                
              
               </span> 
               
              <span className="spandelmenu">
                <li>
                <Link to="/appdownload" style={{ textDecoration: 'none'  }}  >APP</Link>
                </li>
              </span>

             

              <span className="spandelmenu">  
                <li>
                <Link to="/trabajo" style={{ textDecoration: 'none'  }}  >TRABAJA CON NOSOTROS</Link>
                </li>
              </span>  

            </ul> 
               
         

        </details>


        </nav>
       
        

        
        
       </> 
    )

}

