import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,

} from "react-router-dom";
import './App.css';


import  MenumiApp  from "./components/MenumiApp";
import  SetComidas  from "./components/SetComidas"

import {Cvformulario} from './components/Cvformulario'
import { AppDownload } from "./components/AppDownload";
import {Footer} from './components/Footer'
import { LandingPage } from "../src/pages/LandingPage";



function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <div className="grid-container">  

      <nav className="navbar"><MenumiApp/></nav>
      <article className="main">
       <Switch>


          <Route path="/Comidas"><SetComidas /></Route>
          <Route path="/Home"><LandingPage/></Route>
          <Route path="/trabajo"><Cvformulario/></Route>
          <Route path="/appdownload"><AppDownload/></Route>
          <Route path="/"><LandingPage/></Route>
          
         

       </Switch>  
       </article>
      

      <footer className="footer"><Footer></Footer></footer>
    
      </div>
    </Router>
  );
}

export default App;
